<template>
	<div>
		<h6>Los campos en (*) son obligatorios</h6>
		<h4>Registrar nuevo usuario:</h4>

		<b-form>
			<!-- nro_ticket -->
			<b-form-group label="*Usuario"
						  label-for="usuar"
						  @submit.stop.prevent="handleSubmit">
				<b-form-input id="usuar"
							  v-model="usuario.usuar"
							  type="text"
							  placeholder="Ingrese el numero del usuar"
							  :state="validacion.usuar.estado"
							  invalid-feedback="Complete este campo"
							  required>
				</b-form-input>
				<b-form-invalid-feedback id="usuar-live-feedback">
					{{ validacion.usuar.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>
			<b-form-group label="*Contrase�a"
						  label-for="passw"
						  @submit.stop.prevent="handleSubmit">
				<b-form-input id="passw"
							  v-model="usuario.passw"
							  type="password"
							  placeholder="Ingrese la password del usuario"
							  :state="validacion.passw.estado"
							  invalid-feedback="Complete este campo"
							  required>
				</b-form-input>
				<b-form-invalid-feedback id="passw-live-feedback">
					{{ validacion.passw.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>
			<b-form-group label="*Tipo de Cuenta"
						  label-for="tipocuenta"
						  @submit.stop.prevent="handleSubmit">
				<b-select id="tipocuenta"
							  v-model="usuario.tipocuenta"
							  placeholder="Ingrese el tipo de cuenta del usuario"
							  :state="validacion.tipocuenta.estado"
							  invalid-feedback="Complete este campo"
							  required
							:options ="tipos_cuenta">
				</b-select>
				<b-form-invalid-feedback id="tipocuenta-live-feedback">
					{{ validacion.tipocuenta.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>
		</b-form>
		<b-button
			class="mt-2"
			variant="success"
			block
			@click="postUsuario()"
			>Guardar</b-button
		>
	</div>
</template>

<script>
	import { APIControler } from "@/store/APIControler";

	export default {
		props: {
			updateTable: Function,
		},
		data() {
			return {
				usuario: {},
				data: {},
				validacion: {
					usuar: { estado: null, mensaje: "" },
					passw: { estado: null, mensaje: "" },
                    tipocuenta: { estado: null, mensaje: "" },
				},
				respuesta: {},
				tipos_cuenta: [{ value: "administrador", text: 'Administrador' },
                    { value: "encargado", text: 'Encargado de Camara' }],
			};
		},
		methods: {
			async getUsuarios() {
                let UsuariosAPI = new APIControler();
                this.data = await UsuariosAPI.getData();
			},
			async postUsuario() {
				let gastoSalienteAPI = new APIControler();
				gastoSalienteAPI.apiUrl.pathname = "usuarios/";
				this.respuesta = await gastoSalienteAPI.postData(this.usuario);
				this.cargarFeedback();
				this.updateTable();
			},

			cargarFeedback() {
				let valido;
				this.respuesta=this.respuesta|| {}
				for (let key in this.validacion) {
					valido = !this.respuesta.hasOwnProperty(key);
					this.validacion[key].estado = valido;
					//console.log(key);
					if (!valido) this.validacion[key].mensaje = this.respuesta[key][0];
				}
			},
		},
	};
</script>

<style></style>
