<template>
	<div>
		<h6>Los campos en (*) son obligatorios</h6>
		<h4>Datos</h4>

		<b-form>
			<!-- nro_ticket -->
			<b-form-group label="*Usuario"
						  label-for="usuar"
						  @submit.stop.prevent="handleSubmit">
				<b-form-input id="usuar"
							  v-model="usuario.usuar"
							  type="text"
							  placeholder="Ingrese el numero del usuar"
							  :state="validacion.usuar.estado"
							  invalid-feedback="Complete este campo"
							  required>
				</b-form-input>
				<b-form-invalid-feedback id="usuar-live-feedback">
					{{ validacion.usuar.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>
			<b-form-group label="*Password"
						  label-for="passw"
						  @submit.stop.prevent="handleSubmit">
				<b-form-input id="passw"
							  v-model="usuario.passw"
							  type="text"
							  placeholder="Ingrese la password del usuario"
							  :state="validacion.passw.estado"
							  invalid-feedback="Complete este campo"
							  required>
				</b-form-input>
				<b-form-invalid-feedback id="passw-live-feedback">
					{{ validacion.passw.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>
			<b-form-group label="*Tipo de Cuenta"
						  label-for="tipocuenta"
						  @submit.stop.prevent="handleSubmit">
				<b-select id="tipocuenta"
						  v-model="usuario.tipocuenta"
						  placeholder="Ingrese el tipo de cuenta del usuario"
						  :state="validacion.tipocuenta.estado"
						  invalid-feedback="Complete este campo"
						  required
						  :options="tipos_cuenta">
				</b-select>
				<b-form-invalid-feedback id="tipocuenta-live-feedback">
					{{ validacion.tipocuenta.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>
		</b-form>
		<b-button class="mt-2"
				  variant="success"
				  block
				  @click="putUsuario()">Modificar</b-button>
	</div>
</template>

<script>
	import { APIControler } from "@/store/APIControler";
	import axios from "axios";

	export default {
		props: {
			usuario: {},
			updateTable: Function,
		},
		data() {
			return {
                usuarios: {},
				data: {},
				options: [{ value: null, text: "Elija una gasto", disabled: true }],
                tipos_cuenta: [{ value: "administrador", text: 'Administrador' },
                { value: "encargado", text: 'Encargado de Camara' }],
				validacion: {
					usuar: { estado: null, mensaje: "" },
					passw: { estado: null, mensaje: "" },
                    tipocuenta: { estado: null, mensaje: "" },
				},
			};
		},
		created: function () {
			this.getUsuarios();
		},
		methods: {
            async getUsuarios() {
				let gastoSalienteAPI = new APIControler();
				gastoSalienteAPI.apiUrl.pathname = "usuarios/";
				this.data = await gastoSalienteAPI.getData(this.usuarios);
				this.data.forEach((element) => {
					let option = {};
					option.value =
						"http://45.162.169.58:8081/usuarios/" + element.id_usuario + "/";
					option.text = element.usuarios;
					console.log(option);
					this.options.push(option);
				});
			},

			async putUsuario() {
				let respuesta = "vacio";
				await axios
					.put(
						"http://45.162.169.58:8081/usuarios/" +
							this.usuario.id_usuario +
							"/",
						this.usuario
					)
					.then(function (data) {
						swal("Operación Exitosa", " ", "success");
					})
					.catch(function (error) {
						const mje =
							error.response.status < 500
								? "Los datos no son válidos"
								: "Se ha detectado un problema ";
						swal("¡ERROR!", mje, "error");
						respuesta = error.response.data;
					});
				this.cargarFeedback(respuesta);
				this.updateTable();
				// console.log("respuesta:");
				// console.log(respuesta);
			},

			cargarFeedback(respuestaAPI) {
				console.log("respuestaAPI");
				console.log(respuestaAPI);
				let valido;
				for (let key in this.validacion) {
					valido = !respuestaAPI.hasOwnProperty(key);
					this.validacion[key].estado = valido;
					console.log(key);

					if (!valido) this.validacion[key].mensaje = respuestaAPI[key][0];
				}
			},
		},
	};
</script>

<style></style>
